<template>
  <div class="box">
    <form class="signup" @submit="onSubmit">
      <div>
        <div class="level-left">
          <div class="level-item">
            <img
              src="https://www.svgrepo.com/show/300753/barber-shop.svg"
              style="height: 30px;"
            />
          </div>
          <section class="hero">
            <div class="hero-body">
              <div class="container">
                <h1 class="title">
                  Sign up
                </h1>
                <h2 class="subtitle">
                  Register now to begin booking appointments.
                </h2>
              </div>
            </div>
          </section>
        </div>

        <div class="field">
          <p class="control has-icons-left has-icons-right">
            <input
              class="input"
              type="text"
              placeholder="Name"
              v-model="name"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-user-circle"></i>
            </span>
          </p>
        </div>

        <div class="field">
          <p class="control has-icons-left has-icons-right">
            <input
              class="input"
              type="email"
              placeholder="Email"
              v-model="email"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="password"
              placeholder="Password"
              v-model="password"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control">
            <button class="button is-light is-success is-fullwidth">
              Register
            </button>
          </p>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import UserService from "../services/UserService";
export default {
  name: "TheSignup",
  data() {
    return {
      name: "",
      email: "",
      password: "",
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      const newUser = {
        name: this.name,
        email: this.email,
        password: this.password,
      };
      UserService.createUser(newUser);
    },
  },
};
</script>

<style>
.signup {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
